.about {
  margin: 145px auto 40px;
  width: 1318px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 60px auto;
  }

  &__info {
    width: 765px;

    &::before {
      content: '';
      background-image: url('/img/hex.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: right;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0.2;
    }
  }

  &__image-container {
    width: 670px;
    @include center;
    flex-direction: column;
    margin-left: 30px;
    z-index: 2;

    &--who {
      width: 465px;
      margin-left: 0;
      margin-right: 60px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
  }

  &__info-text {
    font-size: 1.6rem;
    text-align: justify;

    &:not(:last-child) {
      margin-bottom: 45px;
    }

    span {
      font-size: 2rem;
      color: $primary;
    }

    &--white {
      color: $white;
    }

    &--bold-white {
      // font-family: $source;
      color: $white;
      font-weight: $bold;
      text-transform: capitalize;
    }

    &--gray {
      color: $gray;
    }
  }

  &__name {
    font-family: $playfair;
    font-size: 2.5rem;
    color: $primary;
    margin: 15px auto;
  }

  &__who {
    // background-color: $primary;
  }

  &__who-text-wrapper {
    width: 500px;
  }

  &__who-wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    // width: 1000px;
    padding: 40px;
  }

  &__who-info {
    position: relative;
    width: auto;
  }

  &__who-logo {
    display: flex;
    width: 200px;
    height: 200px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border: 1px solid;
    }

    &--second {
      margin-left: 50px;
    }
  }

  &__who-text-container {
    // position: absolute;
    // left: -20px;

    &--desc {
      margin-top: 60px;
    }
  }

  &__who-info-title {
    position: relative;
    font-size: 3rem;
    font-family: $playfair;
    // color: $white;

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: -20%;
      width: 100%;
      height: 1px;
      margin-top: 20px;
      background-color: $primary;
    }
  }

  &__who-button-container {
    margin: 20px auto 0;
  }

  &__community {
    position: relative;
    display: flex;
    margin: 60px auto;
  }

  &__community-title-container {
    min-width: 500px;
  }

  &__community-title {
    position: relative;
    font-size: 3rem;
    font-family: $playfair;
    text-align: center;
    color: $primary;

    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 1px;
      margin-top: 20px;
      background-color: $primary;
    }
  }

  &__community-text-container {
    margin: 60px 0 60px 200px;
    width: 565px;
  }

  &__community-text {
    font-size: 1.6rem;
    font-weight: $bold;
    text-align: justify;
    color: $dark-gray;
    margin: 20px auto;
  }

  &__community-details {
    margin-right: 40px;
  }

  &__community-logo {
    display: flex;
    flex-wrap: wrap;
    width: 1000px;
  }


  &__community-logo-img {
    width: 320px;
    height: 160px;
    object-fit: cover;
    object-position: center;
    border: 1px solid $gray;
    margin-top: -1px;
    margin-left: -1px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    &:hover {
      img {
        transition: 0.3s ease-in-out;
        transform: scale(1.1);
      }
    }
  }

  &__community-assoc {
    width: 500px;
  }

  &__information {
    margin: 60px auto;
  }

  &__information-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 25px;
    overflow: hidden;
    @include max-width;
  }

  &__information-item {
    position: relative;
    background-color: $primary;
    min-height: 628px;

    &:hover {
      .about__information-image img {
        transition: 0.3s ease-in-out;
        transform: scale(1.1);
      }

      .button-about-information {
        &:after,
        &:before {
          transform: translate(0);
        }
      }
    }
  }

  &__information-image {
    width: 100%;
    height: 225px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      opacity: 1;
    }
  }

  &__information-text {
    margin: 55px 40px;
    font-family: $source;
    font-weight: $regular;
    color: $white;
    @include center;
    flex-direction: column;

    h1 {
      font-size: 40px;
    }

    p {
      font-size: 16px;
      line-height: getem(16, 32);
      margin: 35px 0;
    }
  }

  &__information-btn-container {
   margin: auto;
  }
}