.contact-lower {
  &__container {
    position: relative;
    width: 100%;
    padding: 60px;
    @include center;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('/img/contact-lower-bg.jpg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      filter: grayscale(100%);
    }
  }

  &__wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    width: 80%;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.767);
    }
  }

  &__info {
    width: 40%;
    z-index: 5;
  }

  &__form {
    width: 60%;
    z-index: 5;
  }

  &__address-wrap {
    margin: 20px auto;
  }

  &__address {
    font-family: $playfair;
    color: $black;
    font-size: 2.4rem;

    &--small {
      font-size: 1.6rem;
      line-height: getem(1.6, 3.2);
      a {
        &:hover {
          transition: 0.3s ease-in-out;
          opacity: 0.5;
        }
      }
    }

    &--medium {
      font-size: 2.4rem;
      line-height: getem(2.4, 4.8);
      a {
        &:hover {
          transition: 0.3s ease-in-out;
          opacity: 0.5;
        }
      }
    }

    &--big {
      font-size: 3.2rem;
      line-height: getem(3.2, 6.4);
      a {
        &:hover {
          transition: 0.3s ease-in-out;
          opacity: 0.5;
        }
      }
    }

    &--form-title {
      font-size: 3.2rem;
      line-height: getem(3.2, 6.4);
      color: $gold;
    }
  }

  &__form-wrap {
    display: flex;
    margin: 20px;
  }

  &__textbox {
    width: 100%;
    height: 50px;
    padding: 20px;
    font-size: 1.6rem;
    border: none;
    background-color: transparent;
    border-bottom: 2px solid $gold;

    &--small {
      width: 50%;

      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }

  &__textarea {
    width: 100%;
    height: 150px;
    padding: 20px;
    font-size: 1.6rem;
    border: none;
    resize: none;
    background-color: transparent;
    border-bottom: 2px solid $gold;

    &::placeholder {
      font-family: $source;
      font-size: 1.6rem;
    }
  }
}