.service-1 {
  position: relative;

  &__hero {
    background-image: url('/img/service-1-hero.jpg');
    background-attachment: fixed;
    background-position-y: -0.5;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    color: white;
    transition: 0s linear;
    transition-property: background-position;
    width: 100%;
    height: 550px;
    @include center;
    flex-direction: column;
    box-shadow:inset 0 0 0 2000px rgba(255, 255, 255, 0.140);
  }

  &__hero-text-wrapper {
    display: flex;
    justify-content: space-between;
    width: 1000px;
    margin-top: 50px;
  }

  &__hero-text {
    font-size: 42px;
    font-weight: $bold;
    color: $black;

    &--big {
      font-size: 58px;
      font-weight: $regular;
    }

    &--small {
      font-size: 34px;
    }
  }

  &__hero-button-container {
    margin-top: 100px;
    width: 330px;
  }
}