.footer {
  position: relative;
  bottom: 0;

  &__copyright {
    position: relative;
    bottom: 0;
    width: 100%;
    height: 38px;
    background-color: $black;
    @include center;
  }

  &__copyright-wrapper {
    display: flex;
    justify-content: space-between;
    // justify-content: space-between;
    width: 1170px;
  }

  &__copyright-text {
    font-family: $source;
    font-size: 12px;
    color: $white;
  }

  &__container {
    background-color: $primary;
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }

  &__section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 400px;
    padding: 90px 35px;

    &:nth-child(2) {
      border-left: 1px solid $white;
      border-right: 1px solid $white;
    }
  }

  &__section-wrap {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }

  &__section-text {
    font-family: $source;
    font-size: 20px;
    font-weight: $semibold;
    line-height: getem(20px, 28px);
    color: $white;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &.sublink {
      font-size: 14px;
      font-weight: $regular;
      margin-left: 20px;
    }
  }

  &__title {
    font-family: $source;
    font-size: 25px;
    font-weight: $semibold;
    line-height: getem(16px, 28px);
    color: $white;
    padding-bottom: 20px;
    
    &::after {
      content: '';
      display: block;
      height: 3px;
      width: 30px;
      background-color: $white;
    }
  }

  &__about {
    font-family: $source;
    font-size: 16px;
    line-height: getem(16px, 28px);
    color: $white;
    margin-top: 20px;
  }

  &__contact {
    display: flex;
    margin-top: 20px;

    img {
      width: 30px;
      height: 30px;
      margin-right: 15px;
    }
  }
}