.banner {
  &--contact {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    padding: 80px 0;
    background-color: $primary;
    margin: 80px auto 40px;
  }

  &--org {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 40px 0;
    height: auto;
    background-color: $primary;
    margin: 80px auto 40px;
    text-align: center;
  }

  &__org-logo-wrapper {
    @include center;
    flex-wrap: wrap;
  }

  &__org-logo {
    margin: 40px 0 0;
    width: 200px;
    height: 200px;
    overflow: hidden;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 20px;
    }

    img {
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: cover;
    }

    &:hover {
      transition: 0.3s ease-in;
      transform: scale(1.1);
    }
  }

  &__wrapper {
    h1 {
      font-family: $playfair;
      font-size: 50px;
      color: $white;
    }

    p {
      font-size: 30px;
      color: $gold;
    }
  }
}