.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 105px;
  padding: 0 200px;
  @include center;
  font: {
    size: 1.8rem;
  }
  line-height: getem(18, 22);
  background-color: rgba(255, 255, 255, 0.555);
  left: 0;
  display: flex;
  align-items: center;
  z-index: 999;
  @include sp {
    height: getsp375(100px);
    padding: 0 0 0 getsp375(20px);
  }

  &__logo {
    width: 300px;
    height: 70px;
    margin-right: auto;
    @include center;
    @include sp {
      width: getsp375(416px);
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }      
  }

  &-nav {
    display: flex;
    @include sp {
      display: none;
    }
  }

  &-nav__link {
    position: relative;
    padding: 33px 20px;
    color: $text;
    cursor: pointer;

    &:hover {
      color: $primary;
    }

    &:nth-child(2):hover {
      .header-nav__submenu {
        max-height: 300px;
      }
    }
  }

  &-nav__submenu {
    position: absolute;
    max-height: 0;
    height: 300px;
    width: 300px;
    color: $white;
    background-color: $primary;
    top: 97px;
    left: -90%;
    transition: .5s;
    overflow: hidden;  
    @include center;
    flex-direction: column;
  }

  &-nav__submenu-link {
    position: relative;
    font: {
      size: 2rem;
      
    }
    line-height: getem(20, 35);
    font-weight: $black;
    letter-spacing: 0.1em;

    h3 {
      font-size: 1.8rem;
      line-height: getem(18, 31.5);
      text-align: center;
    }

    &-container {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
    }
  }
    
  &-container {
    display: flex;
    flex-direction: column;
  }

  &-menu__btn {
    position: relative;
    display: none;
    cursor: pointer;
    @include sp {
      width: getsp375(100px);
      height: 100%;
      background-color: $primary;
      flex-direction: column;
      @include center;
    }

    &-shape {
      width: getsp375(45px);
      height: 1.5px;
      background-color: $white;
      transition: .5s;

      &:not(:last-child) {
        margin-bottom: getsp375(10px);
      }
    }
  }

  &-menu__btn-animate {
    .header-menu__btn-shape {
      position: absolute;
      top: 50%;
      left: 30%;
      transform: translateY(-50%, -30%);

      &:first-child {
        transform: rotate(45deg);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        transform: rotate(-45deg);
      }
    }
  }

  &-menu {
    display: none;
    @include sp {
      position: absolute;
      width: 100%;
      height: calc(100vh - 40px);
      display: flex;
      flex-direction: column;
      background-color: $white;
      top: getsp375(100px);
      right: -100%;
      overflow-y: scroll;
      transition: .5s;
      z-index: 999;
    }
  }

  &-menu__animate {
    right: 0;
  }

  &-menu__contacts {
    display: flex;
    width: 100%;

    &-item {
      width: 50%;
      padding: getsp375(30px) 0;
      font-size: getsp375(3rem);
      color: $white;
      background-color: $primary;
      line-height: getem(getsp375(36), getsp375(72));
      @include center;

      &:first-of-type {
        background-color: $secondary;
        font: {
          size: getsp375(3.6rem);
          
        }
        line-height: getem(getsp375(30), getsp375(52.5));
        

        // &::before {
        //   content: '';
        //   width: getsp375(24px);
        //   height: getsp375(36px);
        //   margin-right: getsp375(30px);
        //   background: {
        //     image: url("./img/slideshow/tel-logo.png");
        //     size: cover;
        //     position: center;
        //   }
        // }
      }
    }
  }

  &-menu__link {
    padding: getsp375(25px) getsp375(40px);
    font: {
      size: getsp375(3rem);
      
    }
    border: {
      width: 0 0 1.5px;
      style: solid;
    }
    line-height: getem(getsp375(30), getsp375(52.5));

    &--s2 {
      padding: getsp375(25px) 0 0;
      border-bottom: unset;

      p {
        padding: 0 getsp375(40px) getsp375(25px);
      }
    }
  }

  &-menu__submenu {
    width: 100%;

    &-link-container {
      display: flex;
      padding-bottom: getsp375(17px);
    }
    
    &-link {
      width: 33.3%;

      h3 {
        font: {
          size: getsp375(2.7rem);
          
        }
        line-height: getem(getsp375(27), getsp375(47.25));
        text-align: center;
      }
    }

    &-img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }

  &-menu__others {
    padding-bottom: getsp375(80px);
    margin: getsp375(60px) 0 0;
    flex-direction: column;
    @include center;
  }
  
  &-menu__socials {
    margin-bottom: getsp375(22px);

    &-link {
      &:not(:last-child) {
        margin-right: getsp375(60px);
      }

      &:first-of-type {
        img {
          width: getsp375(48px);
          height: getsp375(39px)
        }
      }

      img {
        width: getsp375(48px);
        height: getsp375(48px);
      }
    }
  }
    
  &-menu__more {
    &-link {
      font: {
        size: getsp375(2.7rem);
      }
      line-height: getem(getsp375(27), getsp375(47.25));

      &:not(:last-child) {
        margin-right: getsp375(40px);
      }
    } 
  }
}
      
  