.contact {
  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 100vh;
    background-image: 
    linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url('/img/contact-bg.jpg');
    // background-image: url('/img/contact-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 200px 100px 0;
  }

  &__info {
    width: 50%;
    margin-right: 40px;
  }

  &__info-title {
    font-size: 70px;
    font-weight: $bold;
    color: $gold;

    &--small {
      font-size: 48px;
    }
  }

  &__info-text {
    font-family: $roboto;
    font-size: 48px;
    font-weight: $bold;
    color: $white;

    &--small {
      font-size: 24px;
      text-align: justify;
      font-family: Arial, Helvetica, sans-serif;
    }
  }

  &__info-text-wrapper {
    margin: 20px 0;
  }

  &__form {
    width: 50%;
  }

  &__form-wrapper {
    display: flex;
    flex-direction: column;
    margin: 10px 0 0;
  }

  &__form-button-container {
    margin: 20px auto;
    width: 500px;
  }

  &__textbox {
    margin-bottom: 10px;
    height: 57px;
    font-family: $source;
    font-size: 20px;
    padding: 10px;
    &::placeholder {
      color: $gray;
    }
  }

  &__textarea {
    height: 220px;
    font-family: $source;
    font-size: 20px;
    padding: 10px;
    &::placeholder {
      color: $gray;
    }
  }

  &__map-container {
    @include center;
    // width: 1140px;
    margin: 60px auto;
  }
  
  &__map {
    width: 765px;
    height: 553px;
     iframe {
      width: 765px;
      height: 553px;
     }
  }
  
  &__map-info {
    position: relative;
    width: 400px;
    height: 500px;
    background-color: $primary;
    background-image: url('/img/pattern-01.png');
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 60px;
  }

  &__map-info-text {
    font-family: $roboto;
    font-size: 18px;
    font-weight: $light;
    color: $white;
  }

  &__map-info-phone {
    font-family: $roboto;
    font-size: 16px;
    font-weight: $light;
    color: $white;
    margin: 40px 0 65px;

    &:hover {
      transition: 0.3s ease-in-out;
      opacity: 0.5;
    }
  }

  &__map-info-loc {
    position: absolute;
    top: 50px;
    left: -50px;
    width: 193px;
    height: 60px;
    background-color: $gold;
    @include center;

    span {
      font-size: 24px;
      color: $white;
    }
  }
}